import React from 'react';
import VisibilitySensor from 'react-visibility-sensor';

const centeredStyles ={
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'column',
  textAlign:'center',
  height:"100%"
};

const h2Styles = {
  fontSize:"82px"
};
const h3Styles = {
  fontSize:"40px"
};
const margin = {
  marginTop:"80px"
};
const profilePic = {
  height: "300px",
  width: "200px",
  borderRadius: "90px"
};

const Top = () => {
  return(

      <div
        style={{
          ...centeredStyles,
          height: "90vh",
          backgroundColor: "transparent"
        }}
      >
        <VisibilitySensor>
          {({ isVisible }) => (
            <div>
              <a name="top"></a>
            <div style={{...margin}} className={isVisible ? "slideUp enter" : "slideUp"}>
              <img style={{...profilePic}} src="https://i.imgur.com/y5mnBu9.jpg"/>
              <h2 style={{ ...h2Styles }}>COLEMAN HOLMES</h2>
              <h3 style={{...h3Styles}}>USER INTERFACE / USER EXPERIENCE</h3>
              <h3 style={{...h3Styles}}>DESIGNER / DEVELOPER</h3>
            </div>
            </div>
          )}
        </VisibilitySensor>
      </div>
  )
}

export default Top;