import React from 'react';
import VisibilitySensor from 'react-visibility-sensor';

const centeredStyles ={
  position: "sticky",
  top: "0",
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'row',
  textAlign:'center',
  height:"100%",
  zIndex: "100"
};

const margin = {
  marginLeft: "10px",
  marginRight: "10px"
}


const Navbar = () => {
  return(

      <div
        style={{
          ...centeredStyles,
          height: "50px",
          backgroundColor: "transparent"
        }}
      >
        <a style={{...margin}} href="#top">TOP</a>
        <a style={{...margin}} href="#toolbox">TOOLBOX</a>
        <a style={{...margin}} href="#portfolio">PORTFOLIO</a>
        <a style={{...margin}} href="#contact">CONTACT</a>
      
      </div>
  )
}

export default Navbar;