import React from 'react';
import VisibilitySensor from 'react-visibility-sensor';

const centeredStyles ={
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'column',
  textAlign:'center',
  height:"100%"
};

const h2Styles = {
  fontSize:"82px"
};
const h3Styles = {
  fontSize:"40px",
  textAlign: "center",
  marginTop: "20px"
};


const iconStyles ={
  height: "60px",
  width: "60px",
  margin: "10px",
}

const flexRow = {
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'row',
  textAlign:'center',
}
const flexColumn = {
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'column',
  textAlign:'center',
}

const Portfolio = () => {
  return(

      <div
        style={{
          ...centeredStyles,
          height: "90vh",
          marginTop:"120px",
          backgroundColor: "transparent"
        }}
      >
        <VisibilitySensor>
          {({ isVisible }) => (
            <div>
              <a name="portfolio"></a>
              <div className={isVisible ? "slideUp enter" : "slideUp"}>
                <h2 style={{ ...h2Styles }}>PORTFOLIO</h2>
                <div style={{...flexRow}}>
                  <a target="_blank" href="https://taskmaster6000.herokuapp.com">
                    <h3 style={{...h3Styles}}>TASKMASTER6000</h3>
                  </a>
                  <a target="_blank" href="https://taskmaster6000.herokuapp.com">
                    <img style={{...iconStyles}} src="http://cdn.onlinewebfonts.com/svg/img_552202.png"/>
                  </a>
                </div>
                <div style={{...flexRow}}>
                  <a target="_blank" href="https://taskmaster5000.herokuapp.com">
                    <h3 style={{...h3Styles}}>TASKMASTER5000</h3>
                  </a>
                  <a target="_blank" href="https://taskmaster5000.herokuapp.com">
                    <img style={{...iconStyles}} src="https://image.flaticon.com/icons/png/512/53/53591.png"/>
                  </a>
                </div>
                <div style={{...flexRow}}>
                  <a target="_blank" href="https://anyagram.herokuapp.com">
                    <h3 style={{...h3Styles}}>ANY-A-GRAM</h3>
                  </a>
                  <a target="_blank" href="https://anyagram.herokuapp.com">
                    <img style={{...iconStyles}} src="https://image.flaticon.com/icons/png/512/2043/2043832.png"/>
                  </a>
                </div>
                <div style={{...flexRow}}>
                  <a target="_blank" href="https://courtkings.herokuapp.com">
                    <h3 style={{...h3Styles}}>COURTKINGS</h3>
                  </a>
                  <a target="_blank" href="https://courtkings.herokuapp.com">
                    <img style={{...iconStyles}} src="https://image.flaticon.com/icons/png/512/46/46106.png"/>
                  </a>
                </div>
              </div>
            </div>
          )}
        </VisibilitySensor>
      </div>
  )
}

export default Portfolio;