import React from 'react';
import VisibilitySensor from 'react-visibility-sensor';

const centeredStyles ={
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'column',
  textAlign:'center',
  height:"100%"
};

const h2Styles = {
  fontSize:"82px"
};
const h3Styles = {
  fontSize:"15px",
  marginTop:"55px",
  marginBottom:"55px",
};

const profilePicStyles ={
  height: "250px",
  width: "170px",
  margin: "60px",
  borderRadius: "100px",
  border: "5px solid black"
}

const iconStyles ={
  height: "200px",
  width: "400px",
  margin: "40px",
  marginBottom: "0px"
}
const iconWideStyles ={
  height: "120px",
  width: "400px",
  margin: "40px",
  marginTop:"80px",
  marginBottom: "40px"
}

const flexRow = {
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'row',
  textAlign:'center',
}
const flexColumn = {
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'column',
  textAlign:'center',
}

const Intro = () => {
  return(

    <div
    style={{
      ...centeredStyles,
      height: "150vh",
      overflow: "hidden"
    }}
  >
    <VisibilitySensor partialVisibility>
      {({ isVisible }) => (
        <div className={isVisible ? "slideUp enter" : "slideUp"}>
          <h2 style={{ ...h2Styles }}>MY JOURNEY</h2>
            <div style={{...flexRow}}>
              <div style={{...flexColumn}}>
                <img style={{...iconStyles}} src="http://static-assets.generalassemb.ly/logos/generalassembly-open-graph.png" />
                <h2 style={{ ...h3Styles }}>SOFTWARE ENGINEERING IMMERSIVE</h2>
              </div>
              <div style={{...flexColumn}}>
                <img style={{...iconStyles}} src="https://a3.espncdn.com/combiner/i?img=%2Fi%2Fteamlogos%2Fncaa%2F500%2F249.png" />
                <h2 style={{ ...h3Styles }}>COMPUTER SCIENCE 101 AND 201</h2>
              </div>
              <div style={{...flexColumn}}>
                <img style={{...iconWideStyles}} src="https://bims.tamu.edu/wp-content/themes/CVM-Department-FP/assets/img/cvm-logo@2x.png" />
                <h2 style={{ ...h3Styles }}>B.S. BIOMEDICAL SCIENCE</h2>
              </div>
            </div>
          </div>
      )}
    </VisibilitySensor>
  </div>
  )
}

export default Intro;