import React from 'react';
import VisibilitySensor from 'react-visibility-sensor';

const centeredStyles ={
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'column',
  textAlign:'center',
  height:"100%"
};

const h2Styles = {
  fontSize:"82px"
};
const h3Styles = {
  fontSize:"40px",
  textAlign: "center",
  marginTop: "20px"
};


const iconStyles ={
  height: "60px",
  width: "60px",
  margin: "10px",
}

const flexRow = {
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'row',
  textAlign:'center',
}
const flexColumn = {
  display:"flex",
  alignItmes: "center",
  justifyContent:"center",
  flexDirection:'column',
  textAlign:'center',
}

const Contact = () => {
  return(

      <div
        style={{
          ...centeredStyles,
          height: "90vh",
          marginTop:"120px",
          backgroundColor: "transparent"
        }}
      >
        <VisibilitySensor>
          {({ isVisible }) => (
            <div>
              <a name="contact"></a>
              <div className={isVisible ? "slideUp enter" : "slideUp"}>
                <h2 style={{ ...h2Styles }}>CONTACT</h2>
                <h3 style={{...h3Styles}}>email: mitchellcholmes@gmail.com</h3>
                <div style={{...flexRow}}>
                  <a target="_blank" href="https://github.com/41Holmes41">
                    <img style={{...iconStyles}} src="https://image.flaticon.com/icons/svg/25/25231.svg"/>
                  </a>
                  <a target="_blank" href="https://linkedin.com/in/coleman-holmes">
                    <img style={{...iconStyles}} src="https://image.flaticon.com/icons/png/512/61/61109.png"/>
                  </a>
                  <a target="_blank" href="https://www.facebook.com/coleholmes">
                    <img style={{...iconStyles}} src="https://cdns.iconmonstr.com/wp-content/assets/preview/2017/240/iconmonstr-facebook-6.png"/>
                  </a>
                  <a target="_blank" href="https://www.instagram.com/colepop/">
                    <img style={{...iconStyles}} src="http://hivhero.org/wp-content/uploads/2011/11/instagram-512.gif"/>
                  </a>
                </div>
              </div>
            </div>
          )}
        </VisibilitySensor>
      </div>
  )
}

export default Contact;